:root {
  --background: #F5F6F8;
  --foreground: #171717;
  --background-secondary: #F9F8FF;
  --color-black: #3B434E;
  --color-white: #fff;
  --color-primary: #7653FC;
  --color-primary-hover: #5b36ed;
  --color-secondary: #B3C0D9;
  --color-green: #1DC37E;
  --color-light-green: #C8F8E4;
  --color-red: #FC836D;
}

@media (prefers-color-scheme: dark) {
  :root {
    /* --background: #0a0a0a; */
    /* --foreground: #ededed; */
  }
}

html,
body {
  max-width: 100vw;
  overflow-x: hidden;
}

body {
  color: var(--foreground);
  font-size: 14px;
  line-height: 24px;
  font-weight: 400;
  background: var(--background);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

@media (prefers-color-scheme: dark) {
  html {
    color-scheme: dark;
  }
}
